import Vue from 'vue'
import Vuex from 'vuex'
import router from "../router"
Vue.use(Vuex)

export default new Vuex.Store({
    state:{
        cart: [],
        isLoggedIn: false,
        token: '',
        isLoading: false,
        user:[],
    },
    getters:{
        isLoadingState: (state) => {
            return state.isLoggedIn
        },
        users:(state)=>{
            return state.user
        }
    },
    mutations:{
        allusers(state){
            state.user
        },
        initializeStore(state){
            if(localStorage.getItem('cart')){
                state.cart = JSON.stringify(localStorage.getItem('cart'))
            }
            else{
                localStorage.setItem('cart',JSON.stringify(state.cart))
            }
        },
        addToCart(state,item){
            state.cart=JSON.parse(item)
            localStorage.setItem('cart',JSON.stringify(state.cart))
        },
        removeCart(state){
            state.cart=[]
            localStorage.removeItem('cart')
        },
        login(state,user) {
            state.isLoggedIn = true
            state.user = user
            localStorage.setItem('token',JSON.stringify(state.user))
            if(router.currentRoute.name !== "Dashboard" && router.currentRoute.name !== "Vente") {
                if(state.user.priviledge ==='admin'){router.push({name : 'Dashboard'}).catch()}
                else if(state.user.priviledge ==='Vendeur'){router.push({ path : '/vente'}).catch()}
                else{router.push({ path : '/login'}).catch()}
            }
          },
          logout(state) {
            state.isLoggedIn = false;
            localStorage.removeItem('token')
            state.user =[]
            router.push('/login').catch()
            
          },
          print(state){
            // state.isLoggedIn = false
                if(state.isLoggedIn == true){state.isLoggedIn = false}
                else {
                    if(router.currentRoute.name =='Rapport'){
                        state.isLoggedIn = true
                        router.push('/report')
                    }else{
                    state.isLoggedIn = true
                    router.push('/vente')
                    }
                }
          },
        initializelogs(state){
            if(state.user !==null || !router.currentRoute.name =='login'){
                // localStorage.removeItem("token")
                // router.push('/login').catch()
                state.isLoggedIn = true
                let routername = router.currentRoute.name
                state.user =  JSON.parse(localStorage.getItem('token'))
                router.push({name:routername}).catch()
            }
             if(state.user==null || router.currentRoute.name =='login'){
                localStorage.removeItem("token")
                router.push('/login').catch()
                state.isLoggedIn = false
            }else if(state.user!==null){
                state.isLoggedIn=true
                // let routername = router.currentRoute.name
                state.user =  JSON.parse(localStorage.getItem('token'))
                // router.push({name:routername}).catch()
            }
            else{
                router.push({name:'login'}).catch()
            }
        }
    },
    actions:{
        initializeStore({commit}){
            commit('initializelogs')
        },
        isLoggin({commit},item){
            commit('login',item)
            commit('initializelogs')
        },
        isPrinting({commit}){
            commit('print')
        },
        isLogout({commit}){
            commit('logout')
        },
        user({commit}){
            commit('allusers')
        }
    }
})