
<template>
  <v-app>
    <v-main>
      <v-container class="fill-height" fluid>
        <v-row align="center" justify="center">
          <v-col cols="12" sm="12" md="4" xs="12">
            <v-alert text :value="error" border="left" type="error" tile>{{msg}}</v-alert>
            <v-card class="elevation-12">
                <v-row >
                    <v-col cols="12" md="12" xs="12">
                      <v-card-title >
                        <div style="margin:auto; text-align:center;">
                            <img src="@/assets/log/ceyo.jpeg" width="20%" height="20%">
                        </div>
                        <!-- <div
                          class="text-center display-2 text--accent-3"
                        >
                        <v-avatar class="profile"
                        color="grey"
                        width="10%"
                        height="10%"
                        size="68"  
                        tile>
                            <v-img src="@/assets/log/logo.png" ></v-img>
                        </v-avatar>
                        </div> -->
                      </v-card-title>
                        <v-divider></v-divider>
                        <v-form @submit.prevent="signin" method="post"> 
                            <v-card-text class="text-center display-2 text--accent-3">
                                <v-text-field
                                    v-model="username"
                                    label="Email"
                                    type="email"
                                    color="red accent-3"
                                />

                                <v-text-field
                                    label="Password"
                                    v-model="password"
                                    type="password"
                                    color="red accent-3"
                                />
                            </v-card-text>
                            <div class="text-center mt-3">
                                <v-btn rounded color="red accent-3 justify-center" type="submit" @click="signin" dark>SIGN IN</v-btn>
                            </div>
                        </v-form>
                      <br>
                    </v-col>
                  </v-row>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
export default {
  data: () => ({
    msg:null,
    error:false,
    username:'',
    password:'',
  }),
  methods: {
      signin(){
          const form = new FormData()
          form.append('username',this.username)
          form.append('password',this.password)
          this.isLoggedIn=true
          this.$api.post('loginpro',form)
          .then(rs=>{
            if(rs.data.success == 0){
              this.msg = rs.data.message
              this.error = true
              setTimeout(()=>{
                  this.error=false
              },5000)
            }else{
              this.$store.dispatch('isLoggin',rs.data.token);
              // this.$store.commit('login',JSON.stringify(rs.data.token))
            }
          })
      }
  }
};
</script>