                      </v-container>
                    </v-card-text>
                    <v-divider></v-divider>
                    <v-card-actions class="justify">
                      <br>
                      <v-btn text color="warning" @click="dialog=false;clean()">Annuler</v-btn>
                      <v-spacer></v-spacer>
