                            </v-btn>
                            </v-btn-toggle>
                        </template>
                        </v-toolbar>
                    </template>

                    <template v-slot:default="props">
                        <v-row>
                        <v-col
                            v-for="(item,index) in props.items"
                            :key="index"
                            cols="12"
                            sm="6"
                            md="4"
                            lg="3"
                        >
                            <v-card
                                 class="mx-auto"
                                max-width="100%"
                             >
                               <!-- <router-link to="/" text style="font-decoration: none;">                          -->
                                <v-img
                                v-if="item.image !=''"
                                class="white--text align-end"
                                @click="viewimage(item.id )"
                                height="200px"
                                :src="`http://ceyo.vbggom.com/public/uploads/`+item.image"
                                
                                >
                                    <v-card-title class="text--primary">{{item.names}}</v-card-title>
                                </v-img>
                                <v-img v-else :src="imagepreview" >
                                    <v-card-title class="text--primary">{{item.names}}</v-card-title>
                                </v-img>
                                <!-- </router-link>   -->

                                <v-card-subtitle class="pb-0">
                                Qte:  {{item.quantity}}
                                </v-card-subtitle>

                                <v-card-text class="text--primary">
                                <div style="color: #235bab">Price: ${{item.selling_price}}</div>
                                <div style="color: bluelight">PA: ${{item.purchased_price}}</div>

                                <!-- <div>Whitsunday Island, Whitsunday Islands</div> -->
                                </v-card-text>
                
                                <v-card-actions>
                                <v-spacer></v-spacer>

                                <v-btn icon color="red darken-3" @click="editItem(item);dialog=true">
                                    <v-icon>mdi-pencil</v-icon>
                                </v-btn>

                                <v-btn color="red darken-3" @click="viewitem(item.id)" icon>
                                    <v-icon>mdi-eye</v-icon>
                                </v-btn>
                                </v-card-actions>
                            </v-card>
                        </v-col>
                        </v-row>
                    </template>

                    <template v-slot:footer>
                        <v-row
