                    </template>
                    </v-data-iterator>
                </v-container>
            </v-tab-item>
            <v-tab-item>
                <v-card>
                    <v-card-title>
                        <span><small>List des Produits</small></span>
                                <v-spacer></v-spacer>
                                <v-text-field
                        v-model="search"
                        append-icon="mdi-magnify"
                        label="Recherche"
                        single-line
                        hide-details
                                ></v-text-field>
                    </v-card-title>
                    <v-data-table
                    :items="cards"
                    :headers="headers"
                    :search="search"
                    class="elevation-1"
                    >
                    <template v-slot:body="props">
                        <tbody>
                            <tr v-for="(item,index) in props.items" :key="index">
                                <td>{{index+1}}</td>
                                <td><router-link :to="`/item/${item.id}`"><img :src="`http://ceyo.vbggom.com/public/uploads/`+item.image" alt="" width="100"></router-link></td>
                                <td>{{item.names}}</td>
                                <td>{{item.purchased_price}}</td>
                                <td>{{item.selling_price}}</td>
                                <td>{{item.quantity}}</td>
                                <td>{{item.quantity - item.used_qty}}</td>
                            </tr>
                        </tbody>
                    </template>
                </v-data-table>
            </v-card>
