</template>

<script>

  export default {
    data () {
      return {
        alert: false,
        error:false,
        image:'',
        dialog: false,
        itemsPerPageArray: [8, 12],
        search: '',
        tab: null,
        headers: [
          {
            text: '#',
            align: 'start',
            filterable: false,
            value: 'id',
          },
          { text: 'Image', value: 'image' },
          { text: 'Item', value: 'names' },
          { text: 'Prix d\'Achat', value: 'purchased_price' },
          { text: 'Prix de Vente', value: 'selling_price' },
          { text: 'Quantite', value: 'quantity' },
          { text: 'Disp. Qte', value: 'address' },
          
        ],
        msg:null,
        cards: [],
        product: [],
        colors:  ["Blue", "Dark", "White","Pink","Yellow","Orange",],
        item: {names:"",selling_price:'',quantity:"",id:"",purchased_price:"",description:""},
        filter: {},
        sortDesc: false,
        updateimgae:"",
        page: 1,
        itemsPerPage: 8,
        sortBy: 'name',
        keys: [
          'Item',
          'Size',
        ],
        imgPlaceholder:'https://via.placeholder.com/300',
      }
    },
    computed: {
      numberOfPages () {
        return Math.ceil(this.cards.length / this.itemsPerPage)
      },
      filteredKeys () {
        return this.keys.filter(key => key !== 'Name')
      },
      imagepreview(){
        return this.image == "" ? this.imgPlaceholder : this.image;
      }
    },
    methods: {
        clean(){
        this.item={names:"",selling_price:'',quantity:"",id:"",purchased_price:"",description:""}
        this.image = ""
        },
      nextPage () {
        if (this.page + 1 <= this.numberOfPages) this.page += 1
      },
      formerPage () {
        if (this.page - 1 >= 1) this.page -= 1
      },
      updateItemsPerPage (number) {
        this.itemsPerPage = number
      },
      updatepreview(event){
        const file = event.target.files[0];
        this.image = URL.createObjectURL(file);
      },
      editItem(item){
          this.item = item
        //   this.$refs.featured_img.file = item.image
          this.image = 'http://ceyo.vbggom.com/public/uploads/'+item.image
          this.updateimgae= item.image
      },
      viewitem(item){
          this.$router.push('/itemdescription/'+item)
      },
      manipulateproducts(){
          let file  = this.$refs.featured_img.files[0]
      const form = new FormData()
      form.append("names", this.item.names)
      form.append("description", this.item.description)
      form.append("unit_price",this.item.purchased_price)
      form.append("selling_price",this.item.selling_price)
      form.append("quantity",this.item.quantity)
      form.append('operator',this.$store.state.user.token)
      if(file==undefined){
            file = this.image
      }
        form.append("image",file)
      form.append('id',this.item.id)
      this.$api.post('/manipulate_product',form)
            .then(rs=>{
                if(rs.data.success){
                this.msg=rs.data.success
                this.dialog = false
                // this.$refs.featured_img.files[0]=undefined
                // this.image = ""
                this.clean()
                this.alert = true
                this.getpost()
                setTimeout(()=>{
                    this.alert=false
                },5000)
                }
                else{
                    this.msg=rs.data.error
                    setTimeout(()=>{
                        this.error=false
                    },5000)
                }
            })
            .catch(e=>{
                this.msg=e.data
                    setTimeout(()=>{
                        this.error=false
                    },5000)
            })
      },
      getpost(){
          this.$api.get('/products')
            .then(rs=>{
                this.cards = rs.data
            })
      },
      viewimage(item){
          this.$router.push('/item/'+item)
      }
    },
    mounted(){
        this.getpost()
        document.title="Product | VBG"
    },
    // created(){
    //     this.getpost()
    // }

  }
</script>