  },
  computed:{
      item(){
          return this.$store.getters.users
      }
  },
  methods:{
      change(){
          const form  = new FormData()
          form.append('currentpassword',this.currentpassword)
          form.append('newpassword',this.newpassword)
          form.append('confirmpassoword',this.confirmpassowrd)
          form.append('user',this.$store.state.user.token)
          this.$api.post('changePassword',form)
          .then(rs=>{
              if(rs.data.success){
                    this.msg=rs.data.success
                    this.alert = true
                    setTimeout(()=>{
                        this.alert=false
                    },5000)
                    this.allusers()
                    this.clear()
                }
                else{
                    this.msg=rs.data.error
                    this.error = true
                    setTimeout(()=>{
                        this.error=false
                    },5000)
                }
          })
      }
  },
    
}
