<template>
    <v-app>
        <v-subheader light>
            <h2>Vente</h2>
            <v-spacer></v-spacer>
            <small>Vente | <small>Vgb Store</small></small>
        </v-subheader>
        <v-alert text tile :value="alert" dismissible border="left" type="success" dense>{{msg}}</v-alert>
        <v-alert text tile :value="error" dismissible border="left" type="error" dense>{{msg}}</v-alert>
        <v-card width="100%" tile elevation="2">
            <v-card-title>
                <span>Produits</span>
                <v-spacer></v-spacer>
                <v-btn
                    color="error"
                    dark
                    text
                    @click="createvente"
                    >
                    <v-icon>mdi-cart-arrow-right </v-icon>
                    <span>
                        cart ({{total}})
                    </span> 
                </v-btn>
            </v-card-title>
            <v-divider></v-divider>
            <v-card-text>
                    <v-layout wrap row>
                        <v-flex xs12 md6 lg4 sm6 >
                            <v-card
                            tile
                                 class="mx-auto"
                             >
                               <!-- <router-link to="/" text style="font-decoration: none;">                          -->
                                <v-img
                                v-if="item.image !=''"
                                class="white--text align-end"
                                height="100%"
                                :src="imgPlaceholder"
                                
                                >
                                </v-img>
                                <v-img v-else :src="imgPlaceholder" >
                                </v-img>
                                <!-- </router-link>   -->

                                <v-card-subtitle class="pb-0">
                                </v-card-subtitle>

                                <v-card-text class="text--primary">
                                <div style="color: red">Prix: ${{item.selling_price}}</div>
                                <div style="color: bluelight" >Qte & Size Disponibles
                                    <v-container>
                                        <table width="100%" class="">
                                            <thead class="th"> 
                                                <th >No</th>
                                                <th >Size</th>
                                                <th >Qte D.</th>
                                                <th></th>
                                                <th></th>
                                            </thead>
                                            <tbody v-if="size.size !==''">
                                                <tr 
                                            v-for="(item,index) in size "
                                            :key="index"
                                            > 
                                                    <td  class="td">{{index+1}}</td>
                                                    <td  class="td">{{item.size}}</td>
                                                    <td  class="td">{{item.quantity-item.used_qty}}</td>
                                                    <td class="td"><input type="number" v-model="item.qty" size="3"></td>
                                                    <td>
                                                        <v-btn
                                                            color="error"
                                                            text
                                                            @click="addToCart(item)"
                                                            >
                                                            <v-icon>mdi-cart-arrow-down </v-icon>
                                                            <span>
                                                                Add
                                                            </span> 
                                                        </v-btn>
                                                    </td>
                                                </tr>
                                            </tbody>
                                        </table>
                                    </v-container>
                                </div>

                                <!-- <div>Whitsunday Island, Whitsunday Islands</div> -->
                                </v-card-text>
                
                                <v-card-actions>
                                    <v-btn icon color="red darken-3" @click="backproduct">
                                        <v-icon>mdi-chevron-triple-left</v-icon>
                                    </v-btn>
                                    <v-spacer></v-spacer>
                                </v-card-actions>
                            </v-card>

                        </v-flex>
                        <v-flex xs12 md6 lg8 sm6 pl-2>
                            <v-card tile>
                                <v-card-title>
                                    <span><small>List des Produits</small></span>
                                <v-spacer></v-spacer>
                                <v-text-field
                                    v-model="search"
                                    append-icon="mdi-magnify"
                                    label="Recherche"
                                    single-line
                                    hide-details
                                ></v-text-field>
                                </v-card-title>
                                <v-data-table
                                :headers="headers"
                                :items="items"
                                :items-per-page="5"
                                :search="search"
                                 class="elevation-1"
                                >
                                    <template v-slot:body="{items}">
                                        <tbody>
                                            <tr v-for="(user,index) in items" :key="index">
                                                <td>{{index+1}}</td>
                                                <td><img :src="`http://ceyo.vbggom.com/public/uploads/`+user.image" alt="image" width="80" @click="viewimage(user)"></td>
                                                <td>{{user.names}}</td>
                                                <td>$ {{user.selling_price}}</td>
                                                <td>{{user.quantity}}</td>
                                                <td>{{user.description}}</td>
                                                <td>
                                                    <small><i><v-btn x-small color="error" @click="viewimage(user)">
                                                    View 
                                                    <v-icon
                                                        color="darken-4"
                                                        dense flat x-small
                                                    >
                                                        mdi-eye
                                                    </v-icon>
                                                    </v-btn>
                                                    </i></small>
                                                </td> 
                                            </tr>
                                        </tbody>
                                    </template>
                                </v-data-table>
                            </v-card>
                        </v-flex>
                     </v-layout>
            </v-card-text>
        </v-card>
    </v-app>
</template>
<script>

  export default {
    data () {
      return {
        alert: false,
        error: false,
        status: null,  
        search: '',
        item:[],
        msg:null,
        price:'',
        image:'',
        quantity:'',
        names: '',
        carts: {
            items: [],
        },
        headers: [
          {
            text: '#',
            align: 'start',
            filterable: false,
            value: 'id',
          },
          { text: 'Image', value: 'iamge' },
          { text: 'Noms', value: 'names' },
          { text: 'Prix', value: 'selling_price' },
          { text: 'Quantite', value: 'quantity' },
          { text: 'Description', value: 'description' },
          { text: 'Actions', value: '' },
        ],
        size:[{size:'',quantity:'',selling_price:'',qty:1}],
        store:[],
        items: [],
        imgPlaceholder:'https://via.placeholder.com/300',
        newUser: {names:"",email:"",phone:"",address:"",id:"",status:""},
      }
    },
    methods:{
        addToCart(indx){
            if (isNaN(indx.qty) || indx.qty < 1) {
                indx.qty = 1
            }
              
            else{
                let items= indx
                const item = {
                    id: items.id,
                    size: items.size,
                    item: items.productid,
                    names: this.names,
                    image: this.image,
                    quantity:items.qty,
                    price: this.price
                }
                const form = new FormData()
                form.append("item",items.id)
                form.append("qty",items.qty)
                this.$api.post("/manipulate_quantity",form)
                .then(rs=>{
                    if(rs.data.success)
                    {
                        this.carts.items.push(item)
                        this.$store.commit('addToCart',JSON.stringify(this.carts.items))
                        // console.log(this.$store.state.carts)
                        this.mountedl()
                        this.msg = rs.data.success
                        this.alert = true
                        setTimeout(()=>{
                            this.alert=false
                        },5000)
                    }
                    else{
                        this.msg = rs.data.error
                        this.error = true
                        setTimeout(()=>{
                            this.error=false
                        },5000)
                    }
                })
            }  // console.log(item.quantity)
        },
        clear(){
            this.status = null
        },
      backproduct(){
          this.$router.push('/product')
      },
        allitems(){
            this.$api.get('/vente_products')
            .then(rs=>{
                this.items = rs.data
            })
        },
        createvente(){
            this.$router.push('/items')
        },
        viewimage(item){
            this.item = item
            this.image = item.image
            this.imgPlaceholder=`http://ceyo.vbggom.com/public/uploads/`+item.image
            this.$api.get('/getsize/'+ item.productid)
            .then(rs=>{
                this.size = rs.data
                this.names = item.names
                this.price = item.selling_price
            })
            .catch(er=>{
                console.log(er)
            })
        },
        mountedl(){
        this.carts.items = JSON.parse(localStorage.getItem('cart'))
        }
    },
    beforeCreate(){
        this.$store.commit('initializeStore')

    },
    computed:{
        total() {
        return this.carts.items.length
        }, 
        cartTotalLegnth(){
             return this.carts.items.length
      
        }
    },
    mounted(){
        this.allitems()
        this.mountedl()
        document.title="Users | VBG"
    },
    }

  
</script>