import Vue from 'vue'
import VueRouter from 'vue-router'
import Dashboard from '../pages/dashboard'
import Product from '../pages/product'
import Vente from '../pages/vente'
import Users from '../pages/users'
import cart from '../pages/cart'
import login from '../pages/login'
import report from '../pages/report'
import profile from '../pages/profile'
import invoice  from '../pages/invoice'
import signleitem  from '../pages/singleitem'
// import store from '../store'
import description from '../pages/description'

Vue.use(VueRouter)

const routes = [
  {
    path: '/login',
    name: 'login',
    component: login
  },
  {
    path: '/',
    name: 'Dashboard',
    component: Dashboard,
    auth:true
    
  },
  {
    path: '/invoice/:id',
    name: 'Arrivage',
    component: invoice
  },

  {
    path: '/item/:id',
    name: 'size',
    component: signleitem
  },

  {
    path: '/itemdescription/:id',
    name: 'item',
    component: description
  },
  {
    path: '/product',
    name: 'Product',
    component: Product,
  },
  {
    path: '/profile',
    name: 'profile',
    component: profile
  },
  {
    path: '/report',
    name: 'Rapport',
    component: report
  }, 
  {
    path: '/vente',
    name: 'vente',
    component: Vente,
    auth:true
  },
  {
    path: '/users',
    name: 'Utilisateur',
    component: Users
  },
  {
    path: '/items',
    name: 'Ventes',
    component: cart
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.VUE_APP_APPNAME,
  routes
})
router.beforeEach((to, from, next) => {
  if (to.name !== 'login' && localStorage.getItem('token')=== null) next({ name: 'login' })
  else next()
})
// router.beforeEach((to,from,next)=>{
//   if(to.matched.some(record => record.meta.requireLogin) && !store.isLoggedIn==true){
//     next()
//   }else{
//     next()
//   }
// })
export default router
