                this.items = rs.data.items
                this.names = rs.data.items[0].client
                this.today = rs.data.items[0].created_at
            })
            .catch(e=>{
                console.log(e)
            })
      },
      print(){
          document.getElementById('btn').style.display='none'
          window.print();
          this.$store.dispatch('isPrinting')
      },
      back(){
          this.$store.dispatch('isPrinting')
      }
    },
    computed:{
        total(){
            return this.items.reduce(
                (acc, item) => acc + item.amount * item.quantity,
                0
            )
        }
    },
    mounted(){
        this.getdata()
    }
}
