<script>
export default {
  data(){
      return{
          item:[],
          items:[],

      }
  },
  methods:{
      backproduct(){
          this.$router.push('/product')
      },
      product(){
        var id = this.$route.params.id
        this.$api.get(`/description_item/${id}`)
            .then(rs=>{
                this.item = rs.data.product
                this.items = rs.data.items
            })
            .catch(e=>{
                console.log(e)
            })
      },
  },
  mounted(){
      this.product()
  }
    
}
