<template>
    <v-app>
        <v-subheader light>
            <h2>Users</h2>
            <v-spacer></v-spacer>
            <small>Home | <small>Vgb Store</small></small>
        </v-subheader>
        <v-alert text tile :value="alert" dismissible border="left" type="success" dense>{{msg}}</v-alert>
        <v-alert text tile :value="error" dismissible border="left" type="error" dense>{{msg}}</v-alert>
        <v-card width="100%" tile elevation="2">
            <v-card-title>
                <span>Utilisateurs</span>
            </v-card-title>
            <v-divider></v-divider>
            <v-card-text>
                    <v-layout wrap row>
                        <v-flex xs12 md6 lg4 sm6>
                            <v-card>
                                <v-card-title style="background: #FD3957" class="white--text"><span>{{(newUser.id ? "Modification de l'utilisateur" : "Formulaire de l'utilisateur")}}</span></v-card-title>
                                <v-card-text>
                                    <form @submit.prevent="manipulateuser">
                                        <v-divider></v-divider>
                                        <v-card-text> 
                                            <br>
                                                <v-row>
                                                <v-col
                                                    cols="12"
                                                    md="12"
                                                >
                                                    <v-text-field
                                                    v-model="newUser.names"
                                                    :rules="nameRules"
                                                    :counter="50"
                                                    type="text"
                                                    dense
                                                    label="Noms Complet de l'utilisateur"
                                                    required
                                                    ></v-text-field>
                                                </v-col>
                                                <v-col
                                                    cols="12"
                                                    md="6"
                                                >
                                                    <v-text-field
                                                    v-model="newUser.email"
                                                    :counter="50"
                                                    type="email"
                                                    :rules="emailRules"
                                                    dense
                                                    label="Email address"
                                                    ></v-text-field>
                                                </v-col>
                                                <v-col
                                                    cols="12"
                                                    md="6"
                                                >
                                                    <v-text-field
                                                    v-model="newUser.phone"
                                                    :rules="phoneRules"
                                                    :counter="10"
                                                    dense
                                                    type="number"
                                                    label="Telephone"
                                                    required
                                                    ></v-text-field>
                                                </v-col>

                                                <v-col
                                                    cols="12"
                                                    md="12"
                                                >
                                                    <v-text-field
                                                    v-model="newUser.address"
                                                    :rules="addressRules"
                                                    :counter="50"
                                                    type="text"
                                                    dense
                                                    label="Addresse de l'utilisateur"
                                                    ></v-text-field>
                                                </v-col>
                                                <v-col
                                                    cols="12"
                                                    md="6"
                                                >
                                                    <v-select v-model="newUser.priviledge" :items="role" label="Role">

                                                    </v-select>
                                                </v-col>
                                                 <v-col
                                                    cols="12"
                                                    md="6"
                                                >
                                                    <v-select v-model="newUser.status" :items="status" label="Status">

                                                    </v-select>
                                                </v-col>
                                                
                                                </v-row>
                                            </v-card-text>
                                            <br>
                                        <v-divider></v-divider>
                                        <v-card-actions>
                                        <v-btn
                                            color="grye"
                                            rounded
                                            @click="clear"
                                        >

                                        <span >Annuler</span>
                                        </v-btn>
                                        <v-spacer></v-spacer>
                                        <v-btn
                                        dark
                                            color="red accent-3"
                                            rounded
                                            @click="manipulateuser"
                                            type="submit"
                                        >

                                        <span >{{(newUser.id ? "Modifier" : "Enregistrer")}}</span>
                                        </v-btn>
                                        </v-card-actions>
                                        <br>
                                    </form>
                                </v-card-text>
                            </v-card>

                        </v-flex>
                        <v-flex xs12 md6 lg8 sm6 pl-2>
                            <v-card>
                                <v-card-title>
                                    <span><small>List des Utilisateurs</small></span>
                                <v-spacer></v-spacer>
                                <v-text-field
                                    v-model="search"
                                    append-icon="mdi-magnify"
                                    label="Recherche"
                                    single-line
                                    hide-details
                                ></v-text-field>
                                </v-card-title>
                                <v-data-table
                                :headers="headers"
                                :items="users"
                                :items-per-page="5"
                                :search="search"
                                 class="elevation-1"
                                >
                                    <template v-slot:body="{items}">
                                        <tbody>
                                            <tr v-for="(user,index) in items" :key="index">
                                                <td>{{index+1}}</td>
                                                <td>{{user.names}}</td>
                                                <td>{{user.email}}</td>
                                                <td>{{user.phone}}</td>
                                                <td>{{user.address}}</td>
                                                <td>{{user.priviledge}}</td>
                                                <td>{{(user.status==1 ? "Active" : "Desactive")}}</td>
                                                <td>
                                                    <!-- <small><i><v-btn x-small color="success">Reset</v-btn></i></small> -->
                                                    <small><i><v-btn x-small color="error" @click="selctuser(user)">
                                                    Modifier 
                                                    <v-icon
                                                        color=""
                                                        dense flat x-small
                                                    >
                                                        mdi-open-in-new
                                                    </v-icon>
                                                    </v-btn>
                                                    </i></small>
                                                    <!-- <small><i><v-btn x-small color="error" @click="selctuser(user)">
                                                    Supprimer 
                                                    <v-icon
                                                        color="darken-4"
                                                        dense flat x-small
                                                    >
                                                        mdi-delete
                                                    </v-icon>
                                                    </v-btn>
                                                    </i></small> -->
                                                </td> 
                                            </tr>
                                        </tbody>
                                    </template>
                                </v-data-table>
                            </v-card>
                        </v-flex>
                     </v-layout>
            </v-card-text>
        </v-card>
    </v-app>
</template>
<script>

  export default {
    data () {
      return {
        alert: false,
        error:false,
        nameRules: [
            v => !!v || 'Noms du client ne peux pas etre vide',
            // v => v.length <= 50 || 'Noms ne doit pas depasser 50 caracteres',
        ],
            email: '',
            phoneRules: [
                v => !!v || 'Le numero de telephone est obligatoire',
                // v => /.+@.+/.test(v) || 'E-mail must be valid',
            ],
            emailRules: [
                v => !!v || 'Address email est obligatoire',
                v => /.+@.+/.test(v) || 'E-mail doit etre valide',
            ],
            addressRules: [
                // v => !!v || 'Address email est obligatoire',
                v => v.length <= 50 || 'L\' addresse est obligatoire',
            ],  
        search: '',
        headers: [
          {
            text: '#',
            align: 'start',
            filterable: false,
            value: 'id',
          },
          { text: 'Noms', value: 'names' },
          { text: 'Email', value: 'email' },
          { text: 'telephone', value: 'telephone' },
          { text: 'Address', value: 'address' },
          {text: 'Role', value:'priviledge'},
          { text: 'Status', value: 'address' },
          { text: 'Actions', value: '' },
        ],
        users: [],
        msg:null,
        status:['1','0'],
        role:['Admin','Vendeur'],
        newUser: {names:"",email:"",phone:"",address:"",id:"",status:'',priviledge:""},
      }
    },
    methods:{
        clear(){
            this.newUser= {names:"",email:"",phone:"",priviledg:"",address:"",id:"",status:"",priviledge:""},
            this.status = []
        },
        allusers(){
            this.$api.get('/users')
            .then(rs=>{
                this.users = rs.data
            })
        },
        manipulateuser(){
            const form = new FormData()
            form.append('names',this.newUser.names)
            form.append('email',this.newUser.email)
            form.append('phone',this.newUser.phone)
            form.append('address',this.newUser.address)
            form.append('status',this.newUser.status)
            form.append('role',this.newUser.priviledge)
            form.append('id',this.newUser.id)
            this.$api.post('/manipulate_user',form)
            .then(rs=>{
                // console.log(rs.data.message)
                if(rs.data.message){
                    this.msg=rs.data.message
                    this.alert = true
                    setTimeout(()=>{
                            this.alert=false
                    },5000)
                    this.allusers()
                    this.clear()
                }
                else{
                    this.msg=rs.data.error
                    this.error = true
                    setTimeout(()=>{
                            this.error=false
                    },5000)
                }
            })
        },
        formto(obj){
            const fd = new FormData()
            for(var i in obj){
                fd.append(i,obj[i])
            }
            return fd
        },
        selctuser(user){
            this.newUser = user
        }
    },
    mounted(){
        this.allusers()
        document.title="Users | VBG"
    }

  }
</script>