                                                    <td>
                                                    Tel: +243972048241
                                                    <!-- <br> <a href="maito:vbgstore@gmail.com"><span> vbgstore@gmail.com</span></a> -->
                                                    <br><a href="maito:alexydangote@gmail.com"><span> alexydangote@gmail.com</span></a>
