                        </div>
                    </v-col>
                        <v-col cols="12" md="6" lg="6">
                            <v-card tile>
                                <v-card-title>
                                    <span>Clients</span>
                                    <v-spacer></v-spacer>

                                <v-text-field
                                    v-model="search"
                                    append-icon="mdi-magnify"
                                    label="Recherche"
                                    single-line
                                    hide-details
                                ></v-text-field>
                                    <v-btn text color="red" @click="dialog=true" x-small>
                                        <v-icon>mdi-plus</v-icon>
