                                    </v-btn>
                                </v-card-title>
                                <v-divider></v-divider>

                                <v-card-text>
                                    <v-data-table
                                    :headers="headers"
                                    :items="clients"
                                    :items-per-page="10"
                                    :search="search"
                                    class="elevation-1"
                                    >
                                        <template v-slot:body="{items}">
                                            <tbody>
                                                <tr v-for="(item,index) in items" :key="index">
                                                    <td>{{index+1}}</td>
                                                    <td>{{item.names}}</td>
                                                    <td>
                                                        <span>
                                                            <v-btn  @click="setclient(item)" color="red" x-small icon>
                                                                <v-icon>mdi-account-check-outline</v-icon>
                                                            </v-btn>
                                                        </span>
                                                        <span>
                                                            <v-btn color="blue" @click="setuser(item)" icon x-small>
                                                                <v-icon>mdi-pencil</v-icon>
                                                            </v-btn>
                                                        </span>
                                                        <span>
                                                            <v-btn color="black" icon x-small @click="deleteuser(item.id)">
                                                                <v-icon>mdi-delete</v-icon>
                                                            </v-btn>
                                                        </span>
                                                    </td>
                                                    <td>{{item.phone}}</td>
                                                    <td>{{item.email}}</td>
                                                    <td>{{item.address}}</td>
                                                </tr>
                                            </tbody>  
                                        </template>
                                    </v-data-table>
                                </v-card-text>
                            </v-card>
                        </v-col>
                </v-row>
    </v-app>
</template>
<script>
export default {
  data(){
      return{
        alert:false,
        error:false,
        msg:null,
        secure:false,
        items:[],
        item:[],
        id:null,
        security:'',
        clientid:'',
        dialog:false,
        names: null,
        badge:false,
        headers: [
          {
            text: '#',
            align: 'start',
            filterable: false,
            value: 'id',
          },
          { text: 'Noms', },
          { text: 'Actions',},
          { text: 'Phone',},
          { text: 'Email',},
          { text: 'Addresse',},
        ],
        search: '',
        clients:[],
        newclient:{names:'',phone:'',address:'',email:''}
      }
  },
  mounted(){
      this.items = JSON.parse(localStorage.getItem('cart'))
      this.client()
  },
  methods:{
      clean(){
          this.newclient={names:'',phone:'',address:'',email:''}
          this.clientid =''
      },
    //   update_price(id,price){
    //       console.log(id)
    //       console.log(price)
    //   },
      saveinvoice(){
          this.secure = true
        if(this.security !== ''){
            const form1 = new FormData()
            form1.append('security',this.security)
            form1.append('user',this.$store.state.user.token)
            this.$api.post('checkeruser',form1)
            .then(rs=>{
                if(rs.data.user === 'success' ){
                    if(this.items ==''){
                        this.msg = "Vous ne pouvez pas effectuer cette operation"
                        this.error = true
                        setTimeout(()=>{
                        this.error=false
                        },5000)
                    }else{
                        if(this.id ==null){
                            this.id = 1
                        }
                        const form = new FormData()
                        form.append('client',this.id)
                        form.append('items',JSON.stringify(this.items))
                        form.append('token',this.$store.state.user.token)
                        this.$api.post('saveinvoice',form)
                        .then(rs=>{
                            if(rs.data.success){
                                this.$store.commit('removeCart')
                                localStorage.removeItem('cart')
                                this.$router.push('/invoice/'+rs.data.invoice)
                                this.$store.dispatch('isPrinting')
                                this.items =[]
                            }
                        })
                    }
                }
                else{
                    this.secure = false
                    this.msg = rs.data.error
                    this.error = true
                    setTimeout(()=>{
                    this.error=false
                    },5000) 
                }
            })
        }
        else{
            this.secure = false
            this.msg = "Votre Code ne correspond pas!"
            this.error = true
            setTimeout(()=>{
            this.error=false
            },5000) 
        }
        
      },
      annulercart(){
          if(this.items==''){
            this.$store.commit('removeCart')
            localStorage.removeItem('cart')
            this.$router.push('/vente')

          }
          else{
            this.msg = "Veuillez vider la feuille d'abord!"
            this.error = true
            setTimeout(()=>{
            this.error=false
            },5000)
          }
        //   const form = new FormData()
        // //   form.append('client',this.id)
        //   form.append('items',JSON.stringify(this.items))
        //   this.$api.post('removeInvoice',form)
        //   .then(rs=>{
        //       if(rs.data.success){
        //       }
        //   })
      },
      saveclient(){
          const form = new FormData()
          form.append("names",this.newclient.names)
          form.append("phone",this.newclient.phone)
          form.append("email",this.newclient.email)
          form.append("address",this.newclient.address)
          form.append("id",this.clientid)
          this.$api.post('/manipulate_client',form)
          .then(rs=>{
              if(rs.data.success){
                this.msg = rs.data.success
                this.alert = true
                setTimeout(()=>{
                    this.alert=false
                },5000)
                this.clean()
                this.dialog = false
                this.client()
              }else{
                this.msg = rs.data.error
                this.error = true
                setTimeout(()=>{
                    this.error=false
                },5000)  
              }
          })
      },
      deleteuser(index){
        this.$api.get('deleteclient/'+index)
        .then(rs=>{
            if(rs.data.success){
                this.msg = rs.data.success
                this.client()
                this.alert = true
                setTimeout(()=>{
                this.alert=false
                },5000)
            }
            else{
                this.msg = "Desole! Vous ne pouvez pas effectuer cette operation"
                this.error = true
                setTimeout(()=>{
                this.error=false
                },5000)
            }

        })
      },
      setclient(client){
          this.names = client.names
          this.id = client.id
      },
      setuser(user){
          this.newclient = user
          this.clientid= user.id
          this.dialog= true
      },
      removeItem(index,item){
        this.items.splice(index,1)
        let form = new FormData()
        form.append('id',item.id)
        form.append('qty',item.quantity)
        this.$api.post('reset_quantity',form)
        .then(rs=>{
            if(rs.data.success){
                this.msg = rs.data.success
                this.alert = true
                setTimeout(()=>{
                this.alert=false
                },5000)
            }
            else{
                this.msg = rs.data.success
                this.error = true
                setTimeout(()=>{
                this.error=false
                },5000)
            }
        })
      },
      client(){
          this.$api.get('/clients')
            .then(rs=>{
                this.clients = rs.data
            })
      },
  },
  computed: {
    total() {
      return (this.items || []).reduce(
        (acc, item) => acc + item.price * item.quantity,
        0
      )
    },
    today(){
        var currentdate = new Date(); 
        var datetime = "Date: " + currentdate.getDate() + "/"
                + (currentdate.getMonth()+1)  + "/" 
                + currentdate.getFullYear() + " Time:"  
                + currentdate.getHours() + ":"  
                + currentdate.getMinutes() + ":" 
                + currentdate.getSeconds();
        return datetime
    }
  },

    
}
</script>