<template>
    <v-app>
        <v-subheader light>
            <h2>Mis a jour du produit - <span style='color:red'>{{item.names}}</span></h2>
            <v-spacer></v-spacer>
            <small>Home | <small>Vbg Store</small></small>
        </v-subheader>
        <v-alert text tile :value="alert" dismissible border="left" type="success" dense>{{msg}}</v-alert>
        <v-alert text tile :value="error" dismissible border="left" type="error" dense>{{msg}}</v-alert>
        <br>
        <v-card tile>
            <v-flex ml-2 mr-2>
                <v-row>
                        <v-col
                            cols="12"
                            sm="6"
                            md="4"
                            lg="4"
                        >
                            <v-card
                            tile
                                 class="mx-auto"
                                max-width="100%"
                             >
                               <!-- <router-link to="/" text style="font-decoration: none;">                          -->
                                <v-img
                                v-if="item.image !=''"
                                class="white--text align-end"
                                height="100%"
                                :src="`http://ceyo.vbggom.com/public/uploads/`+item.image"
                                
                                >
                                </v-img>
                                <v-img v-else :src="imagepreview" >
                                </v-img>
                                <!-- </router-link>   -->

                                <v-card-subtitle class="pb-0">
                                Qte:  {{item.quantity}}
                                </v-card-subtitle>

                                <v-card-text class="text--primary">
                                <div style="color: red">Price: ${{item.selling_price}}</div>
                                <div style="color: bluelight">PA: ${{item.purchased_price}}</div>

                                <!-- <div>Whitsunday Island, Whitsunday Islands</div> -->
                                </v-card-text>
                
                                <v-card-actions>


                                <v-btn color="red darken-3" @click="backproduct" icon>
                                    <v-icon>mdi-chevron-triple-left</v-icon>
                                </v-btn>
                                <v-spacer></v-spacer>

                                <v-btn color="red darken-3" @click="viewitem(item.id)" icon>
                                    <v-icon>mdi-eye</v-icon>
                                </v-btn>
                                </v-card-actions>
                            </v-card>
                        </v-col>
                        <v-col
                            cols="12"
                            sm="6"
                            md="4"
                            lg="8"
                        >
                            <v-card
                                style="margin-top:1px"
                                tile
                                 class="mx-auto"
                                max-width="100%"
                             >  <br>
                                <v-container>
                                    <form action="">
                                        <v-card-title>
                                            <v-row>
                                                <v-col
                                                    cols="12"
                                                    md="6"
                                                >
                                                    <span> <small>Quantite Globale</small>  - <span style="color:red">{{item.quantity}}</span></span>
                                                </v-col>
                                                <v-col
                                                    cols="12"
                                                    md="3"
                                                >
                                                    <v-btn 
                                                    text
                                                    small
                                                    color="default"
                                                    @click="addfield"
                                                    >
                                                    <span>Ajoouter Size</span>
                                                    </v-btn>
                                                </v-col>

                                                <v-col
                                                    cols="12"
                                                    md="3"
                                                >
                                                    <v-btn 
                                                    text
                                                    color="error"
                                                    @click="savesize"
                                                    >
                                                    <span>Enregister</span>
                                                    </v-btn>
                                                </v-col>
                                            </v-row>    
                                        </v-card-title>
                                        <v-divider></v-divider>
                                        <v-card-text>
                                            <v-container>
                                            <v-row 
                                                        v-for="(item,index) in items"
                                                        :key="index"
                                                    >
                                                    
                                                    <v-col md='5'>
                                                        <input model="item.id" hidden="" type="hidden">
                                                        <v-text-field :label="'Size '+ ++index" v-model="item.size" type="number"></v-text-field>
                                                    </v-col>
                                                     <v-col md='2'   >
                                                        <v-btn @click="removefield(index,item.id)" text x-small color="red accent-5"><v-icon>mdi-archive-off</v-icon></v-btn>
                                                    </v-col>
                                                    <v-col md='5'>
                                                    <v-text-field :label="'Quantity Size '+ index" v-model="item.quantity" type="number"></v-text-field>
                                                    </v-col>
                                            </v-row>
                                            </v-container>
                                        </v-card-text>
                                    </form>
                                </v-container>
                            </v-card>
                        </v-col>
                </v-row>
            </v-flex>
        </v-card>
    </v-app>
</template>
<script>
export default {
  data(){
      return{
          alert:false,
          error:false,
          msg:null,
          item:[],
          field:'1',
          items:[{size:[],quantity:[],id:[]}],

      }
  },
  methods:{
      addfield(){
          var row = this.field
          for(row; row <= this.field; row++){
            row = this.field
            this.items.push({size:[],quantity:[],id:[]})
          }
      },
      removefield(index,item){
          var row =index
          this.items.splice(row,1)
          if(item !=''){
          this.$api.post('delete_size/'+item)
          .then(rs=>{
              if(rs.data.success){
              this.product()
              this.alert =true
              this.msg = rs.data.success
              setTimeout(()=>{
                    this.alert=false
                },5000)
              }
              else{
                this.error =true
                this.msg = rs.data.success
                setTimeout(()=>{
                    this.error=false
                },5000)
              }
          })
          }
      },
      backproduct(){
          this.$router.push('/product')
      },
      viewitem(item){
          this.$router.push('/itemdescription/'+item)
      },
      savesize(){
          const form = new FormData()
          form.append('size',JSON.stringify(this.items))
          form.append('id',this.item.id)
          this.$api.post('manipulate_size',form)
          .then(res=>{
              if(res.data.success){
              this.product()
              this.alert =true
              this.msg = res.data.success
              setTimeout(()=>{
                    this.alert=false
                },5000)
              }
              else{
                this.error =true
                this.msg = res.data.success
                setTimeout(()=>{
                    this.error=false
                },5000)
              }
          })
      },
      product(){
        var id = this.$route.params.id
       this.$api.get(`/description_item/${id}`)
            .then(rs=>{
                this.item = rs.data.product
                this.items = rs.data.items
            })
            .catch(e=>{
                console.log(e)
            })
      },
  },
  mounted(){
      this.product()
  }
    
}
</script>