                                                <span>
                                                <v-btn x-small color="default" text @click="print" id="btn">
                                                    <v-icon>mdi-printer</v-icon>
                                                    <span>Imprimer</span>
                                                    
                                                </v-btn>

                                                <v-btn x-small color="default" text @click="annuler" id="btnannuler">
                                                    <v-icon>mdi-printer</v-icon>
                                                    <span>Annuler</span>
                                                    
                                                </v-btn>
                                                </span>
