</template>
<script>
export default {
  data(){
      return{
          items:[],
          from:'',
          until:'',
      }
  },
  methods:{
      report(){
      let form = new FormData()
        form.append('from',this.from)
        form.append('until',this.until)
        this.$api.post('manipulate_report',form)
        .then(rs=>{
            // console.log(rs.data)
            this.items =rs.data.invoices
        })
      },
      print(){
        //   this.$store.dispatch('isPrinting')
            if(this.$store.dispatch('isPrinting')){
                document.getElementById('formreport').style.display='none'
                document.getElementById('btn').style.display='none'

                document.getElementById('btnannuler').style.display='block'
            }else{
                this.$store.dispatch('isPrinting')
            }
        },
    annuler(){
        this.$store.dispatch('isPrinting')
        document.getElementById('formreport').style.display='block'
        document.getElementById('btn').style.display='block'
        document.getElementById('btnannuler').style.display='none'
    }
  },
  computed:{
    total() {
      return this.items.reduce(
        (acc, item) => acc + item.quantity * item.amount,
        0
      )
    },
    today(){
        var currentdate = new Date(); 
        var datetime = "Faite le: " + currentdate.getDate() + "/"
                + (currentdate.getMonth()+1)  + "/" 
                + currentdate.getFullYear() + " Huere:"  
                + currentdate.getHours() + ":"  
                + currentdate.getMinutes()
        return datetime
    }
  }
    
}
</script>
<style scoped>
    #btnannuler{
    display:none;
    }
</style>